.search-bar-container {
    margin-bottom: 0.25rem;
}

.search-row {
    display: flex;
    align-items: center;
    gap: 1rem;
}

.search-input-wrapper {
    position: relative;
    display: flex;
    align-items: center;
    margin-right: .75rem;
    flex-grow: 1;
    margin-bottom: 0 !important;
}

.search-input-wrapper .prefix {
    position: absolute;
    left: .5rem;
    top: 50%;
    transform: translateY(-50%);
    color: #574D37;
}

.search-icon {
    width: 24px;
    height: 24px;
    flex-shrink: 0;
}

.search-input {
    padding-right: 2.25rem !important;
    font-family: 'Libre Baskerville', serif !important;
    font-size: 1rem !important;
    border-bottom: 1px solid #574D37 !important;
    box-sizing: border-box !important;
}

.search-input:focus {
    border-bottom: 2px solid #574D37 !important;
    box-shadow: none !important;
}

.search-input::placeholder {
    color: #999;
    font-style: italic;
}

.clear-icon {
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
    color: #574D37;
    font-size: 1.2rem;
}

.clear-icon:hover {
    color: #333;
}

.search-field-select {
    flex-shrink: 0;
    margin-right: .75rem;
    min-width: 130px;
    margin-bottom: 0 !important;
    font-family: 'Libre Baskerville', serif !important;
}

.search-results-count {
    margin: 0.25rem 0 0 2.75rem;
    font-size: 0.85rem;
    color: #666;
    font-style: italic;
}

.material-icons.prefix.active {
    color: #574d37 !important;;
}

/* Mobile: stack search input and field select vertically */
@media screen and (max-width: 600px) {
    .search-row {
        flex-direction: column;
        align-items: stretch;
        gap: 0.5rem;
    }

    .search-field-select {
        min-width: unset;
        margin-left: 0.75rem;
    }
}